import styled from "styled-components";

import { colors, fontFamily, fontSizes } from "../../styleguide";


export const LINE_HEIGHT = 15;

export interface StyledTextAreaProps {
	$isInvalid?: boolean,
	$isDisabled?: boolean,
	widthSize?: string,
	resize?: "vertical" | "none" | "horizontal",
	positionErrorMessage?: "right" | "left",
}

export const LabelWrapper = styled.div<StyledTextAreaProps>`
	font-size: ${fontSizes.s};
	line-height: 15px;
	font-family: ${fontFamily.larsseit};
	color: ${(props) => {
		if (props.$isInvalid) {
			return colors.red;
		}
		return colors.black;
	}};
`;

export const ContainerTextArea = styled.div<StyledTextAreaProps>`
	width: ${({ widthSize }) => widthSize};
`;

export const StyledTextArea = styled.textarea<StyledTextAreaProps>`
	resize: ${({ resize }) => resize};
	font-family: ${fontFamily.larsseit};
	font-size: ${fontSizes.s};
	border: 1px solid ${colors.defaultGray};
	margin-top: 5px;
	padding: 10px 8px 10px 16px;
	width: ${({ widthSize }) => widthSize};
	background: ${colors.white};
	box-sizing: border-box;
	border-radius: 4px;
	line-height: ${LINE_HEIGHT}px;

	&:focus {
		outline-offset: 2px;
		outline: 3px solid ${colors.midBlue}33;
	}

	border-color: ${(props) => {
		if (props.$isDisabled) {
			return colors.defaultGray;
		}
		if (props.$isInvalid) {
			return colors.red;
		}
		return colors.defaultGray;
	}};
`;

export const Message = styled.p<StyledTextAreaProps>`
	font-family: ${fontFamily.larsseit};
	text-align: ${({ positionErrorMessage }) => positionErrorMessage};
	color: ${colors.darkerGray};
	font-size: 12px;
	margin-top: 10px;
	margin-bottom: 0;
`;

export const ErrorMessageSpan = styled.span`
	color: ${colors.red}
`;
